
.map {
    width: 48px;
    height: 560px;
    position: absolute;
    top: 36px;
    right: 8px;
    background: black;
}

.map_biomes {
    display: flex;
    flex-direction: column-reverse;
    margin: 0;
    padding: 0;
    list-style: none;
    height: 100%;
}

.map_biome_section {
    display: block;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.75);
    background-image: url("../assets/textures/gameMap.png");
    transform: rotate(180deg)
}

.map_biome_grass     { background-position:    0   0; }
.map_biome_swamp     { background-position:  -58px 0; }
.map_biome_woods     { background-position: -116px 0; }
.map_biome_mountains { background-position: -174px 0; }
.map_biome_ruins     { background-position: -232px 0; }
.map_biome_portal    { background-position: -290px 0; }
.map_biome_hell      { background-position: -348px 0; }

.map .player {
    background-image: url("../assets/textures/TX Props.png");
    background-position: -9px -228px;
    width: 26px;
    height: 27px;
    position: absolute;
    bottom: 0;
    left: 12px;
    transition: margin 700ms;
}
.map .boss {
    background-image: url("../assets/textures/TX Props.png");
    background-position: -44px -231px;
    width: 18px;
    height: 20px;
    position: absolute;
    bottom: 0;
    left: 17px;
}
.map .portal {
    background-image: url("../assets/textures/TX Props.png");
    background-position: -19px -260px;
    width: 38px;
    height: 20px;
    position: absolute;
    bottom: 0;
    left: 6px;
}
