html {
    background-color: #282c34;
    background-image: url("assets/textures/checkeredGrunge.png");
    background-position: center;
    background-repeat: repeat;
    background-size: 218px;
}

.App {
    text-align: center;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* font-size: calc(10px + 2vmin); */
    color: white;
    background: radial-gradient(transparent 40%, #000);
}

.App-body,
.App-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.gameStats {
    text-align: center;
    font-size: 18px;
    position: absolute;
    z-index: 160;
}

.gameStats_turns {
    line-height: 38px;
    text-align: center;
    top: 12px;
    left: 178px;
    color: #fff;
    font-weight: bold;
    text-shadow: hsla(0, 0%, 15%, 0.5) 0 -1px 0, hsla(0, 0%, 0%, 0.6) 0 2px 1px;
    width: 82px;
    height: 38px;
    margin: 0 2px;
    background-image: url("assets/textures/TX Props.png");
    background-position: -198px -448px;
}



.gameStats_hearts {
    top: 25px;
    left: 75px;
    display: flex;
    flex-direction: row;
}

.heart {
    width: 15px;
    height: 14px;
    margin: 0 2px;
    background-image: url("assets/textures/TX Props.png");
    background-position: -134px -457px;
}
.heart_full {}
.heart_empty { background-position: -154px -457px; }

.gameStats_charges {
    top: 23px;
    right: 75px;
    display: flex;
    flex-direction: row-reverse;
}

.charge {
    width: 16px;
    height: 16px;
    margin: 0 2px;
    background-image: url("assets/textures/TX Props.png");
    background-position: -134px -394px;
}
.charge_full {}
.charge_empty { background-position: -134px -424px; opacity: 0.5 }

.frame * { user-select: none; }
.frame {
    position: relative;
    width: 445px;
    height: 666px;
    background: url("assets/textures/gameFrame.png") top right no-repeat;
}

.viewPort {
    height: 560px;
    width: 373px;
    margin: 36px 8px 10px 8px;
    position: relative;
    overflow: hidden;
}

.viewPort:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 16px;
    box-shadow: inset 0 6px 6px 0 black;
    z-index: 20;
}

.box {
    position: absolute;
    margin-bottom: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
}


.field {
    z-index: 10;
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    flex-direction: column-reverse;
}

.movementModal {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: black;
    opacity: 0.25;
    z-index: 100;
}




.logo {
    background-image: url("assets/textures/TX Props.png");
    background-position: -348px -480px;
    width: 150px;
    height: 28px;
    position: absolute;
    bottom: 19px;
    left: 85px;
}

.version {
    background-image: url("assets/textures/TX Props.png");
    background-position: -220px -366px;
    width: 95px;
    height: 35px;
    position: absolute;
    bottom: 15px;
    right: 90px;
    font-size: 14px;
    line-height: 35px;
}

.info-bubble {
    background-image: url("assets/textures/TX Props.png");
    background-position: -354px -444px;
    width: 36px;
    height: 36px;
    position: absolute;
    bottom: 13px;
    right: 11px;
    cursor: pointer;
}




@keyframes fadeInAndOut {
    0%,100% { opacity: 0 }
    25% { opacity: 1 }
}

.pewPew {
    position: absolute;
    z-index: 300;
    opacity: 1;
    animation: fadeInAndOut 0.3s linear;
}

.pewPew_NW { top: 12px; left:  57px; }
.pewPew_NE { top: 12px; right: 60px; }
.pewPew_SW { bottom: 79px; left: 57px; }
.pewPew_SE { bottom: 79px; right: 60px; }

.pewPew_NE, .pewPew_SW {
    background:
        linear-gradient(to top left,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 2.8px),
        rgb(75, 175, 250) 50%,
        rgba(0,0,0,0) calc(50% + 2.8px),
        rgba(0,0,0,0) 100%);
}
.pewPew_NW, .pewPew_SE {
    background:
        linear-gradient(to top right,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 2.8px),
        rgb(75, 175, 250) 50%,
        rgba(0,0,0,0) calc(50% + 2.8px),
        rgba(0,0,0,0) 100%);
}

.charges_depleted .pewPew_NE, .charges_depleted .pewPew_SW {
    background:
        linear-gradient(to top left,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 2.8px),
        rgb(250, 150, 75) 50%,
        rgba(0,0,0,0) calc(50% + 2.8px),
        rgba(0,0,0,0) 100%);
}
.charges_depleted .pewPew_NW, .charges_depleted .pewPew_SE {
    background:
        linear-gradient(to top right,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0) calc(50% - 2.8px),
        rgb(250, 150, 75) 50%,
        rgba(0,0,0,0) calc(50% + 2.8px),
        rgba(0,0,0,0) 100%);
}



.candy_mod .cell_basic { background-image: url("assets/textures/TX Tileset candy.png"); }
.candy_mod .cell_crater .cell_background { background-position: -429px -444px; }
.candy_mod .cell_dud .cell_background { background-position: -389px -444px; }
.candy_mod .cell_blank .cell_background { background-position: -469px -444px; }

.retro_mod .cell_basic { background: #ccc; }
.retro_mod .cell_crater .cell_background { background-position: -429px -412px; }
.retro_mod .cell_dud .cell_background { background-position: -389px -412px; }
.retro_mod .cell_blank .cell_background { background-position: -469px -412px; }
