



.win_background {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    z-index: 500;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    text-align: center;
    font-size: 16px;
    opacity: 1;
    animation: fade-in 10s linear;
    background: rgb(0,0,0);
    background: linear-gradient(
        180deg,
        rgba(0,0,0,0) 0%,
        rgba(0,0,0,0.75) 25%,
        rgba(0,0,0,0.75) 75%,
        rgba(0,0,0,0) 100%
    );
}
.win_background p {
    color: darkgreen;
    font-family: OptimusPrinceps, sans-serif;
    letter-spacing: 5px;
    font-size: 8em;
    font-weight: 400;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
    animation: fade-in-text 6s linear, text-zoom 1.5s linear;
}

.win_stuff {
    position: absolute;
    top: 50%;
    left: 50%;
    animation: fade-in-button 2s linear;
}

.win_reload_button {
    letter-spacing: 2px;
    font-size: 2em;
    font-weight: 400;
    animation: fade-in-button 3s linear;
}

@keyframes fade-in-text {
    0% { opacity: 0; }
    25% { opacity: 1; }
    100% { opacity: 1; }
}
@keyframes fade-in-button {
    0% { opacity: 0; }
    85% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes text-zoom {
    0% { transform: scale(0.4) }
    100% { transform: scale(1.0) }
}
