.board {
    display: flex;
    flex-direction: column-reverse;
    z-index: 1;
}
.board_section {
    display: flex;
    flex-direction: column-reverse;
}

.row {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.stage_base  {
    flex-grow: 0;
    flex-shrink: 0;
    width: 47px;
    height: 32px;
    line-height: 32px;
    margin: 0 8px 0 0;
    font-size: 12px;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.75);
}
.board_section .row:first-child .stage_base { border-bottom: 2px solid rgba(0, 0, 0, 0.6); box-sizing: border-box }
.board_section_0  .stage_base { background-color: #50F246; }
.board_section_1  .stage_base { background-color: #49FCCB; }
.board_section_2  .stage_base { background-color: #4EC3FC; }
.board_section_3  .stage_base { background-color: #5292E6; }
.board_section_4  .stage_base { background-color: #433BF5; }
.board_section_5  .stage_base { background-color: #6733F2; }
