.infoModal {
    text-align: left;
    background: beige;
    border: 3px solid saddlebrown;
    border-radius: 5px;
    padding: 10px;
    color: black;
    position: absolute;
    top: 50%;
    left: 50%;
    height: 670px;
    width: 400px;
    margin-top: -335px;
    margin-left: -200px;
    box-sizing: border-box;
    font-size: 14px;
    box-shadow: 0 0 5px #000, inset 0 0 5px #000;
    z-index: 500;
}

.infoModal_logo {
    background-image: url("../../assets/textures/TX Props.png");
    width: 151px;
    height: 23px;
    background-position: -349px -482px;
}

.infoModal_header {
    text-align: center;
    margin: 16px 0 6px;
}

.infoModal p {
    font-size: 12px;
}

.infoModal_list { padding-left: 25px; }
.infoModal_list li {
    margin-bottom: 6px;
}

.infoModal_tabs {
    text-align: center;
    position: absolute;
    top: 12px;
    right: 30px;
}

.infoModal_tabs_button {
    text-align: center;
    margin: 0 6px;
}
.infoModal_tabs_button.active {
    color: #fff;
    background-color: #666;
}

.infoModal_close {
    cursor: pointer;
    text-align: center;
    position: absolute;
    top: -10px;
    right: -10px;
    width: 32px;
    height: 32px;
    line-height: 24px;
    font-size: 16px;
    font-weight: bold;
    box-sizing: border-box;
    border: 2px solid darkred;
    border-radius: 8px;
    background: red;
    color: white;
    text-shadow: hsla(0, 0%, 15%, 0.5) 0 -1px 0, hsla(0, 0%, 0%, 0.6) 0 2px 1px;
}

.infoModal_media {
    display: flex;
    flex-direction: row;
    margin-bottom: 10px;
}

.infoModal_media h3 {
    margin: 6px 0;
}
.infoModal_media p {
    font-size: 12px;
}
.infoModal_media p:last-child {
    margin-bottom: 0;
}
.infoModal_media.media_left { flex-direction: row-reverse; }
.infoModal_media.media_right {}
.infoModal_media.media_top { flex-direction: column; }
.infoModal_media.media_bottom { flex-direction: column-reverse; }


.infoModal_imageWrapper {
    position: relative;
}
.media_left .infoModal_imageWrapper { margin-left: 10px; }
.media_right .infoModal_imageWrapper { margin-right: 10px; }
.media_top .infoModal_imageWrapper { margin: 0 auto 10px; }
.media_top h3 { text-align: center; margin-top: 0; }
.media_bottom h3 { text-align: center; }
.media_bottom .infoModal_imageWrapper { margin: 10px auto 0; }

.infoModal_image {
    background-image: url("../../assets/textures/infoModal.png");
    background-size: 750px;
}
.infoModal_image.image_fade {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 510;
    animation: flickerAnimation 5s infinite;
}
.infoModal_image.image_01 {
    width: 179px;
    height: 179px;
    background-position: -15.5px -15.5px;
}
.infoModal_image.image_02 {
    width: 179px;
    height: 179px;
    background-position: -210.5px -15.5px;
}
.infoModal_image.image_03 {
    width: 218px;
    height: 78.5px;
    background-position: -15.5px -200.5px;
}
.infoModal_image.image_04 {
    width: 195.5px;
    height: 152.5px;
    background-position: -240.5px -200.5px;
}
.infoModal_image.image_05 {
    width: 147px;
    height: 147px;
    background-position: -201px -359px;
}
.infoModal_image.image_06 {
    width: 179.5px;
    height: 179.5px;
    background-position: -15.5px -285px;
}
.infoModal_image.image_07 {
    width: 179.5px;
    height: 179.5px;
    background-position: -15.5px -470px;
}
.infoModal_image.image_08 {
    width: 265.3px;
    height: 50px;
    background-position: -10.3px -437.3px;
    background-size: 500px;
}

@keyframes flickerAnimation {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
}
