.board_cell {
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    position: relative;
    background: darkgray;
    color: #282c34;
    box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.25);
    border-color: transparent;
}

.cell_grass      { background-image: url("../../assets/textures/TX Tileset grass.png"); }
.cell_swamp      { background-image: url("../../assets/textures/TX Tileset swamp.png"); }
.cell_woods      { background-image: url("../../assets/textures/TX Tileset woods.png"); }
.cell_mountains  { background-image: url("../../assets/textures/TX Tileset mountains.png"); }
.cell_ruins      { background-image: url("../../assets/textures/TX Tileset ruins.png"); }
.cell_portal     { background-image: url("../../assets/textures/TX Tileset portal.png"); }
.cell_hell       { background-image: url("../../assets/textures/TX Tileset hell.png"); }
.cell.transition { background-image: url("../../assets/textures/TX Tileset transitions.png"); }

.cell_x_0 { background-position-x: -00px; }
.cell_x_1 { background-position-x: -32px; }
.cell_x_2 { background-position-x: -64px; }
.cell_x_3 { background-position-x: -96px; }
.cell_x_4 { background-position-x: -128px; }
.cell_x_5 { background-position-x: -160px; }
.cell_x_6 { background-position-x: -192px; }
.cell_x_7 { background-position-x: -224px; }
.cell_y_0 { background-position-y: -00px; }
.cell_y_1 { background-position-y: -32px; }
.cell_y_2 { background-position-y: -64px; }
.cell_y_3 { background-position-y: -96px; }
.cell_y_4 { background-position-y: -128px; }
.cell_y_5 { background-position-y: -160px; }
.cell_y_6 { background-position-y: -192px; }
.cell_y_7 { background-position-y: -224px; }

.cell_from_hell_to_end { background-position-y: -00px; }
.cell_from_portal_to_hell { background-position-y: -32px; }
.cell_from_ruins_to_portal { background-position-y: -64px; }
.cell_from_mountains_to_ruins { background-position-y: -96px; }
.cell_from_woods_to_mountains { background-position-y: -128px; }
.cell_from_swamp_to_woods { background-position-y: -160px; }
.cell_from_grass_to_swamp { background-position-y: -192px; }
.cell_from_start_to_grass { background-position-y: -224px; }
