.stats_headline {
    text-shadow: hsla(0, 0%, 15%, 0.5) 0 -1px 0, hsla(0, 0%, 0%, 0.6) 0 2px 1px;
    margin:0;
}
.stats_board {
    list-style: none;
    text-align: left;
    padding:0;
    border:5px solid #CCC;
    background: rgba(40,40,40,0.75);
    border-radius: 20px;
}

.stats_board li{
    position: relative;
    color:#FFF;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    padding:2% 5%;
    border-bottom: 1px solid #555;
    font-weight:bold;
}

.stats_board li:last-child{
    border-bottom:none;
}

.stat_value {
    display:inline-block;
    float:right;
    color:#F00;
}
