.field_cell {
    flex-grow: 0;
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    line-height: 30px;
    text-align: center;
    box-sizing: border-box;
    position: relative;
    color: white;
    text-shadow:
        -1px -1px 0 #000,
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
}


.cell_empty {}
.cell_hidden {}
.cell_visible {}

.cell_crater {}
.cell_dud    {}
.cell_blank  {}

.cell_visible:not(.cell_bomb) {}

.cell_background {
    width: 32px;
    height: 32px;
}

.cell_hidden .cell_background {
    background-color: #303030;
    opacity: 0.7;
    background-image:  repeating-linear-gradient(45deg, #212121 25%, transparent 25%, transparent 75%, #212121 75%, #212121), repeating-linear-gradient(45deg, #212121 25%, #303030 25%, #303030 75%, #212121 75%, #212121);
    background-position: 0 0, 8px 8px;
    background-size: 16px 16px;
}
.cell_crater .cell_background {
    background-image: url("../../assets/textures/TX Props.png");
    background-position: -96px -288px;
}
.cell_dud .cell_background {
    background-image: url("../../assets/textures/TX Props.png");
    background-position: -96px -352px;
}
.cell_blank .cell_background {
    background-image: url("../../assets/textures/TX Props.png");
    background-position: -96px -415px;
}
